*{margin:0;padding:0;}
#markdown-container{
    margin: 5rem auto 2.5rem auto;
    background-color: #252525;
    border: 1px solid #7b7b7b;
    padding: 2em;
    line-height: 1.4em;
    font: 16px -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,"Noto Sans",sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji";
    color: #d8d8d8;
}
#markdown-container table{
    font:13.34px helvetica,arial,freesans,clean,sans-serif;
    color:#d8d8d8;
	width:100%;
}
#markdown-container table.small{width:auto}
#markdown-container p {
    margin:1em 0;
    line-height:1.5em;
}

#markdown-container input[type=text],
#markdown-container input[type=password],
#markdown-container input[type=image],
#markdown-container textarea{font:99% helvetica,arial,freesans,sans-serif;}
#markdown-container select,
#markdown-container option{padding:0 .25em;}
#markdown-container optgroup{margin-top:.5em;}

#markdown-container img{border:0;max-width:100%;}#markdown-container img.center{display:block;margin:auto}
#markdown-container abbr{border-bottom:none;}
#markdown-container a{color:#00ceff;text-decoration:none;}
#markdown-container a:hover{text-decoration:underline;color:#00a5cc;}
#markdown-container a code,a:link code,a:visited code{color:#00a5cc;}
#markdown-container code {
    color: #ffa6a6;
}
#markdown-container pre code {
    background-color: transparent;
    font-size: 100%;
}
#markdown-container h2,h3{margin:1em 0;}
#markdown-container h1,
#markdown-container h2,
#markdown-container h3,
#markdown-container h4,
#markdown-container h5,
#markdown-container h6{border:0;}
#markdown-container h1{font-size:38px;border-bottom:1px solid #5f5f5f;padding-bottom:.4em;margin-top:1.5em;}
#markdown-container h1:first-child{margin-top:0;padding-top:.25em;border-top:none;margin-bottom:.1em}
#markdown-container h2{font-size:30px;margin-top:1.5em;border-bottom:1px solid #5f5f5f;}
#markdown-container h3{font-size:25px;margin-top:1em;border-bottom:1px solid #5f5f5f}
#markdown-container h4{font-size:22px;border-bottom:1px solid #5f5f5f}
#markdown-container h5{font-size:20px;border-bottom:1px solid #5f5f5f}
#markdown-container h6{font-size:18px;border-bottom:1px solid #5f5f5f}
#markdown-container hr{border:1px solid #ddd;}
#markdown-container ul{margin:1em 0 1em 2em;}
#markdown-container ol{margin:1em 0 1em 2em;}
#markdown-container ul li,
#markdown-container ol li{margin-top:.5em;margin-bottom:.5em;}
#markdown-container ul li{list-style-type:disc;}
#markdown-container ul ul li, #markdown-container ol ul li{list-style-type:circle;}
#markdown-container ol li{list-style-type: decimal;}
#markdown-container ul ul,
#markdown-container ul ol,
#markdown-container ol ol,
#markdown-container ol ul{margin-top:0;margin-bottom:0;}
#markdown-container blockquote{margin:1em 0;border-left:5px solid #ddd;padding-left:.6em;background:#3f3f3f;color:#d8d8d8;}
#markdown-container dt{font-weight:bold;margin-left:1em;}
#markdown-container dd{margin-left:2em;margin-bottom:1em;}

#markdown-outline{
    margin-top: 5rem;
    border:1px solid #7b7b7b;
    padding: 5px 10px;
    max-width: 22%;
    float: right;
    background-color: #252525;
    overflow-y: auto;
	color: #d8d8d8;
}

#markdown-outline ul{
    margin:5px 0 5px 0;
    padding-left:20px;
    font-size:12px;
}
#markdown-outline li{
    list-style-type:square;
    margin:3px 0;
}

#markdown-backTop{
    position: fixed;
    right:5px;
    bottom:5px;
    padding:5px;
    width:50px;
    height:20px;
    background:url(../img/mdreader_backTop.gif) no-repeat 5px 5px #fff;
    border:1px solid #ccc;
    cursor:pointer;
}

/*table style*/

#markdown-container table {
    border-collapse:collapse !important;
}
#markdown-container table th,
#markdown-container table td{
    border:1px solid #868686;
    padding: 5px 10px;
    line-height: 1.5;
    vertical-align: top;
    border-top: 1px solid #868686;

}
#markdown-container table th{
    background: #d8d8d8;
	color:#000
}
#markdown-container table p{
    margin: 0;

}
#markdown-container header {
    border-bottom: 4px solid #5f5f5f;
	margin-bottom: 2em;
}
#disqus_thread{clear:both}

@media (max-width: 1200px) {
    #markdown-outline {
        display: none !important;
    }
}

@media (max-width: 768px) {
    #markdown-container {
        margin: 1rem 0 0 0;
        padding: 1.5rem;
        overflow-x: auto;
    }

    #post-title {
        font-size: 3rem;
    }
}